import Dialog from "../UI/Dialog";
import { CurrentGame } from "../../game/Game";

const ResultsDialog = ({ show, inputs, dismiss }) => {
    return (
        <Dialog
            show={show}
            icon="fa-trophy"
            title="Success"
            content={<ResultsContent inputs={inputs} solution={CurrentGame.getSolution().data} />}
            dismiss={dismiss}
        />
    );
};

const checkAltSolution = (inputs, solution) => {
    return !inputs.every((v, i) => v === solution[i]);
};

const ResultsContent = ({ inputs, solution }) => {
    const puzzleUrl = process.env.PUBLIC_URL + "/#/?puzzle=" + CurrentGame.getEncodedFromSettings().data;
    return (
        <>
            <div>
                You have solved this puzzle{checkAltSolution(inputs, solution) && ` and found an alternate solution`}!
            </div>
            <div className="btn btn-default" onClick={() => { navigator.clipboard.writeText(puzzleUrl) }}>
                Share
            </div>
        </>
    );
};

export default ResultsDialog;